                type="email"
                name="user-email"
                placeholder="账户名/手机号/Email"
              />
            </div>
            <div class="form-line">
              <label for="password">密码</label>
              <input
                v-model="password"
                class="input-data shadow"
                type="password"
                name="user-pass"
                placeholder="请输入您的密码"
              />
            </div>
            <div class="form-line">
              <!-- <label for="password">密码</label> -->
              <input
                type="text"
                class="input-data shadow"
                v-model="verifyImg"
                placeHolder="请输入您的验证码"
              />
              <img
                @touchstart="handleToVerifyImg"
                src="/api/users/verifyImg"
              />
            </div>

            <router-link to="/mine/findPassword">忘记密码？</router-link>
            <div class="form-line login">
              <input
                type="submit"
                value="登 录"
                name="bt-login"
                @touchstart="handleToLogin"
                onclick="return false;"
              />
            </div>
          </div>
        </form>

