<script>
import { messageBox } from '@/components/JS'

export default {
  name: 'Login',
  data() {
    return {
      username: '',
      password: '',
      verifyImg: '',
    }
  },
  methods: {
    handleToLogin() {
      var This = this
      // console.log("验证码"+ this.verifyImg)
      this.axios
        .post('/api/users/login', {
          account: this.username,
          password: this.password,
          verifyImg: this.verifyImg,
        })
        .then((res) => {
          var status = res.data.status
          console.log(res.data)

          if (status === 0) {
            messageBox({
              title: '登录',
              content: '登录成功',
              ok: '确定',
              handleOk() {
                This.$router.push('/mine/center')
                // This.$router.push('/mine/center').catch(() =>{})
                //   This.$router.replace({
                //   path: This.$route.path,
                //   query: This.otherQuery
                //   })
                // .catch(()=>{});
              },
            })
          } else {
            messageBox({
              title: '登录',
              content: res.data.msg,
              ok: '确定',
            })
          }
        })
    },
    handleToVerifyImg(ev) {
      console.log('更新验证码')
      ev.target.src =
        '/api/users/verifyImg?' + Math.random()
    },
  },
}
</script>

